import React from "react";
import Sidebar from "../Sidebar/Sidebar.jsx";
import '../Category/Category.scss';
import './NewsDetail.scss';

const NewsDetail = ( { abstract, category, date,id, hostName,title,  url, urlToImage,  news} ) =>{
  

  // transform date
  const transformDate = (publicDate) => {
    let date = new Date(publicDate).getDate();
    let month = new Date(publicDate).getMonth() +1;
    return ` ${date}.${month < 10 ? '0'+month : month } `;
  }
//
  return(
    <>
      <div className="category">
        <section className="section section__category">
          
          <h2 className="section__title">{category}</h2>
          <div className="article" key={id}>
            <h1 className="article__title">{title}</h1>
            <div className="item__wrap">
              <img src={urlToImage} alt="foto" className="item__img"/>
            </div>
            
            <div className="item__details"><span>Дата : </span>{transformDate(date)} / {category} / {hostName}</div> 
            <p>{abstract}</p>
          </div>
          <hr/>
          
          <div className="comments__list">
            <h2>Comments</h2>
            <div className="comments__item">
              <div className="comments__avatar">
                <img src="https://randomuser.me/api/portraits/men/61.jpg" alt="ava"/>
              </div>
              <div className="comments__details">
                <div className="comments__username">John Smith</div>
                <div className="comments__published">30.06 18:20</div>
                <p className="comments__text"> Consectetur adipisicing elit. Iusto reprehenderit nulla sunt rerum totam saepe, ad fuga aperiam veritatis consequuntur assumenda eos libero sequi similique eaque a illo omnis quisquam.</p>
              </div>
            </div>
            <div className="comments__item">
              <div className="comments__avatar">
                <img src="https://randomuser.me/api/portraits/women/72.jpg" alt="ava"/>
              </div>
              <div className="comments__details">
                <div className="comments__username">Jane Smith</div>
                <div className="comments__published">30.06 20:48</div>
                <p className="comments__text"> Iusto reprehenderit nulla sunt rerum totam saepe, ad fuga aperiam veritatis consequuntur assumenda eos libero sequi similique eaque a illo omnis quisquam.</p>
              </div>
            </div>
            <form className="comments__form">
              <h2>Leave your comment</h2>
              <input type="text" name="name" className="input"/>
              <span>Name</span>
              <input type="text" name="email" className="input"/>
              <span>Email</span>
              <textarea name="message" className="area"></textarea>
              <span>Message</span>
              
              <button type="submit" id="contact_send" value="Send" className="contacts__button">Add comment</button>
            </form>
          </div>
        
        </section>
        <Sidebar news={news}/>
      </div>
    </>
  )
};

export default NewsDetail;